import React, {CSSProperties, memo} from 'react';
import Button from '@mui/material/Button';

/**   :Button (  Material UI)
 *
 *  :   (title),    (CallBack)
 *  : ClassName (  ), style ( ), variant (c Material UI)
 *
 * */


type ButtonFilterPropsType = {
    disabled?: boolean
    title: string
    callBack: () => void
    className?: string
    style?: object
    variant?: "text" | "outlined" | "contained"
}


export const ButtonFilterTask: React.FC<ButtonFilterPropsType> = memo((props) => {
    const {title, callBack, className, disabled,style,variant,...otherProps} = props

    const onclickButtonHandler = () => {
        callBack()
    }

    return (
        <Button
            style={style}
            variant={variant}
            className={className}
            disabled={disabled}
            onClick={onclickButtonHandler}
        >
            {title}
        </Button>
    );
});

